.shipList {
  font-size: 1.3rem;
  background-image: linear-gradient(
    to left,
    rgba(50, 50, 50, 0.5),
    rgba(0, 0, 0, 1)
  );
  color: rgb(16, 71, 114);
  padding: 0.3rem 1.6rem 0.3rem 1.6rem;
  width: 18%;
  list-style: none;
  margin: 0;
  height: 100%;
  /* border: black solid; */
  border-top: 0;
  border-bottom: 0;
  outline: none;
}

.shipList li {
  cursor: pointer;
  padding: 0.5rem;
}

.shipList .currentShipOnList {
  border: rgb(162, 162, 162) solid;
  background: rgb(16, 71, 114);
  color: rgb(162, 162, 162);
  border-radius: 8px;
  font-weight: bold;
}
