body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.shipInfo {
  display: flex;
  justify-content: flex-start;
}

.App {
  background-image: url(/React-SpaceX-SupportShips/static/media/deepspace.f1075a30.jpg);
  /* background-color: red; */
  height: 100%;
  width: auto;
  /* background-size: cover; */
}

.headerWithLogo {
  display: flex;
  /* background-color: rgba(0, 0, 0, 0.666); */
  height: 100px;
  width: 100%;
  /* border: black solid; */
  background-image: -webkit-gradient(
    linear,
    right top, left top,
    from(rgba(0, 0, 0, 0)),
    to(rgba(0, 0, 0, 1))
  );
  background-image: -webkit-linear-gradient(
    right,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 1)
  );
  background-image: linear-gradient(
    to left,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 1)
  );
}

.spaceHeader {
  /* background-color: darkblue;
  height: 5rem;
  width: 90%; */
  width: 90%;

  font-size: 2.5rem;
  font-family: "Audiowide", cursive;
  font-weight: normal;
  color: rgb(16, 71, 114);
  padding: 1.5rem 15rem 1rem 2.5rem;
  box-sizing: border-box;
  margin: 0;
}

.headerWithLogo img {
  object-fit: contain;
  max-width: 600px;
  max-height: 80px;
  /* width: auto; */
  /* height: auto; */
  /* margin-left: 5px; */
  margin: 8px;
}

.shipList {
  font-size: 1.3rem;
  background-image: -webkit-gradient(
    linear,
    right top, left top,
    from(rgba(50, 50, 50, 0.5)),
    to(rgba(0, 0, 0, 1))
  );
  background-image: -webkit-linear-gradient(
    right,
    rgba(50, 50, 50, 0.5),
    rgba(0, 0, 0, 1)
  );
  background-image: linear-gradient(
    to left,
    rgba(50, 50, 50, 0.5),
    rgba(0, 0, 0, 1)
  );
  color: rgb(16, 71, 114);
  padding: 0.3rem 1.6rem 0.3rem 1.6rem;
  width: 18%;
  list-style: none;
  margin: 0;
  height: 100%;
  /* border: black solid; */
  border-top: 0;
  border-bottom: 0;
  outline: none;
}

.shipList li {
  cursor: pointer;
  padding: 0.5rem;
}

.shipList .currentShipOnList {
  border: rgb(162, 162, 162) solid;
  background: rgb(16, 71, 114);
  color: rgb(162, 162, 162);
  border-radius: 8px;
  font-weight: bold;
}

.singleShip {
  background-image: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(50, 50, 50, 0.5)),
    to(rgba(0, 0, 0, 1))
  );
  background-image: -webkit-linear-gradient(
    left,
    rgba(50, 50, 50, 0.5),
    rgba(0, 0, 0, 1)
  );
  background-image: linear-gradient(
    to right,
    rgba(50, 50, 50, 0.5),
    rgba(0, 0, 0, 1)
  );
  padding: 1rem 5rem 1rem 2rem;
  width: 80%;
  color: rgb(16, 71, 114);
  display: flex;

  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.singleShip ul {
  list-style-type: none;
  font-size: 1rem;
  padding: 0;
  margin-top: 10px;
  margin-left: 10px;
}

.singleShip img {
  height: 300px;
  max-width: 500px;
  border-radius: 15px;
  border: black solid;
}

.singleShip label {
  display: inline-block;
  margin-left: 4px;
  margin-right: 8px;
  font-size: 1.4rem;
  margin-left: 10px;
}

.singleShip div {
  width: 35%;
}

.singleShip h1 {
  font-family: "Audiowide", cursive;
}

.singleShip .mainShipData {
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(0, 0, 0, 0.5)),
    color-stop(49%, rgba(0, 0, 0, 1)),
    to(rgba(0, 0, 0, 0))
  );
  background: -webkit-linear-gradient(
    left,
    rgba(0, 0, 0, 0.5) 0%,
    rgba(0, 0, 0, 1) 49%,
    rgba(0, 0, 0, 0) 100%
  );
  background: linear-gradient(
    to right,
    rgba(0, 0, 0, 0.5) 0%,
    rgba(0, 0, 0, 1) 49%,
    rgba(0, 0, 0, 0) 100%
  );
  border-radius: 15px;
}

.singleShip .shipTitle {
  background: rgb(162, 162, 162);
  padding-left: 40px;
  border: black solid;
  border-radius: 15px;
  margin-left: 70px;
  height: 300px;
  padding-right: 40px;
}

.singleShip .shipMissions {
  margin-left: 70px;
}

